<template lang="html">
  <div>

    <v-toolbar
      dense
      floating
      dark
      class="ma-4"
    >
      <h1>{{ now }}</h1>
    </v-toolbar>
    <v-row>

      <v-col cols="12" class="mt-12">
        <div v-if="show" class="animated" v-bind:class="animated">
          <h1 class="text-center display-4 rainbow"></h1>
        </div>
        <div v-else class="animated" v-bind:class="animated">
          <h1 class="text-center display-4 rainbow">N</h1>
        </div>
      </v-col>
      <v-col class="mt-12">


        <v-row v-if="rejection" :justify="'center'">
          <v-col cols="8">
            <v-alert
            prominent
            type="error"
          >
            <v-row align="center">
              <v-col></v-col>
            </v-row>
          </v-alert>
          </v-col>
        </v-row>


        <v-form v-else v-model="valid">
          <v-row  :justify="'center'">
            <v-col cols="3">

              <v-text-field
                v-model="number"

                label=""
                hint=""
                outlined
                rounded
                persistent-hint
                :rules="rules"

                color="cyan"
              ></v-text-field>
            </v-col>
            <v-col cols="1">
              <v-fab-transition>
                <v-btn
                  class="pink"
                  fab
                  dark
                  required
                  v-show="valid"
                  @click="send()"
                >
                  <v-icon>mdi-send</v-icon>
                </v-btn>

              </v-fab-transition>
            </v-col>
          </v-row>
        </v-form>
      </v-col>
    </v-row>

    <v-dialog
      v-model="dialog"
      max-width="1200"
    >
      <NowSeatWrapCard/>
    </v-dialog>

    <v-fab-transition>
      <v-btn
        color="cyan"
        fab
        dark
        fixed
        x-large
        right
        bottom
        outlined
        @click="dialog = true"
      >
        <v-icon>mdi-account-group</v-icon>
      </v-btn>

    </v-fab-transition>

    <v-col cols="2" class="qrCord">
      <v-img
        :src="qr"
      ></v-img>
    </v-col>

  </div>



</template>

<script>
import firebase from 'firebase/app'
import 'firebase/database'
import 'firebase/storage'
import NowSeatWrapCard from './NowSeatWrapCard'

export default {
  data() {
    return {
      now: '',
      number: '',
      show: true,
      inAnimated: '',
      outAnimated: '',
      middleAnimated: '',
      color: '',
      animated: '',
      rules: [v => v.length > 0 || ''],
      valid: true,
      rejection: false, // 
      dialog: false,
      qr: ''//QR
    }
  },
  components: {
    NowSeatWrapCard
  },
  created() {
    this.getQR()
    this.getNow()
    setInterval(function() {
      this.animation()
    }.bind(this), 6000)
  },
  methods: {
    getNow() {
      setInterval(function() {
        let now = new Date()
        this.now = now.toLocaleString()
        this.changeScreen(now)
      }.bind(this), 1000)
    },
    async animation() {
      let list = await this.getInAndOut()
      this.inAnimated = list[0]
      this.outAnimated = list[1]
      this.middleAnimated = list[2]
      this.show = !this.show

      this.animated = list[0]
      setTimeout(function() {
        this.animated = list[2]
        setTimeout(function() {
          this.animated = list[1]
        }.bind(this), 2000)
      }.bind(this), 2000)
    },
    getInAndOut() {
      let inList = ['bounceIn','bounceInDown','bounceInLeft','bounceInRight','bounceInUp']
      let outList = ['bounceOut','bounceOutDown','bounceOutLeft','bounceOutRight','bounceOutUp']
      let middleList = ['bounce','flash','pulse','rubberBand','shake','swing','tada','wobble','jello','heartBeat']
      let inRandom = ''
      let outRandom = ''
      let middleRandom = ''
      let self = this
      return new Promise(function(resolve) {
        // while
        let loop = true
        while (loop) {
          inRandom = inList[Math.floor( Math.random() * 5 )]
          if (self.inAnimated != inRandom) {
            loop = false
          }
        }
        // out
        loop = true
        while (loop) {
          outRandom = outList[Math.floor( Math.random() * 5 )]
          if (self.outAnimated != outRandom) {
            loop = false
          }
        }
        // middle
        loop = true
        while (loop) {
          middleRandom = middleList[Math.floor( Math.random() * 10 )]
          if (self.middleAnimated != middleRandom) {
            loop = false
          }
        }
        resolve([inRandom,outRandom,middleRandom])
      })
    },
    dataReset(target) { // nil nil
      let database = firebase.database()
      for (let i=0; i<15; i++) {
        for (let j=0; j<6; j++) {
          database.ref(`seats/${target}/${i}/${j}`).set('nil')
        }
      }
      console.debug(`${target}:`)
    },
    changeScreen(now) { // (9:30)
      if (now.getHours() == 9 && now.getMinutes() == 30 && now.getSeconds() == 0) {
        let week = ["Mon","Tue","Wed","Thu","Fri"]
        let dayOfWeek = week[now.getDay()-1]

        this.rejection = true
        setTimeout(()=> {
          this.dataReset(dayOfWeek)
          setTimeout(()=> {
            this.rejection = false
          }, 5000)
        }, 5000)
      } else if (now.getHours() == 16 && now.getMinutes() == 0 && now.getSeconds() == 0) { // (16:00)Now
        this.rejection = true
        setTimeout(()=> {
          this.dataReset("Now")
          setTimeout(()=> {
            this.rejection = false
          }, 5000)
        }, 5000)
      } else if (now.getSeconds() == 55) {
        // attendanceData
        this.cacheAttendanceNumber()
        console.debug("")
      }
    },
    cacheAttendanceNumber() { // attendance
      let now = new Date()

      let database = firebase.database()
      let attendance = database.ref('attendance/')
      attendance.on('value', (snapshot)=> {
        let val = snapshot.val()
        delete val['nil'] // 

        for (let key in val) {
          let timeLimitData = val[key]['timeLimit']
          let timeLimit = new Date()
          timeLimit.setHours(timeLimitData['Hours'])
          timeLimit.setMinutes(timeLimitData['Minutes'])

          if (timeLimit.getHours() == now.getHours() && timeLimit.getMinutes() <= now.getMinutes()) {
            database.ref(`attendance/${key}`).remove()
            console.debug(`${key}`)
          } else if (timeLimit.getMinutes() == 0 && now.getMinutes() == 0) { // x:00
            database.ref(`attendance/${key}`).remove()
            console.debug(`${key}`)
          }


        }

      })

    },
    send() {
      console.log(this.number)
      this.$router.push(`/attendance_result/${this.number}`)
    },
    getQR() {
      let storage = firebase.storage()
      let imgRef = storage.ref('QR.png')
      imgRef.getDownloadURL().then((url)=> {
        let xhr = new XMLHttpRequest()
        xhr.responseType = 'blob'
        xhr.onload = ()=> {
          let blob = xhr.response
          this.qr = URL.createObjectURL(blob)
        }
        xhr.open('GET', url)
        xhr.send()
      })
    }
  }
}
</script>

<style lang="css" scoped>
.rainbow {
  /*  200%  */
  background: linear-gradient(to right, #f00 0%, #f80 14.28%, #dd0 28.56%, #0d0 42.85%, #0dd 57.14%, #00f 71.42%, #e0e 85.71%, #f00 100%) 0% center / 200% auto;

  /*  */
          background-clip: text;
  -webkit-background-clip: text;

  /*  color: transparent  color: rgba(0, 0, 0, 0)  */
          text-fill-color: transparent;
  -webkit-text-fill-color: transparent;

  /*  */
  animation: rainbow 20s linear infinite;
}

/*  */
@keyframes rainbow {
  to { background-position-x: 200%; }
}

.qrCord {
  position: fixed;
  left: 10px;
  bottom: 10px;
}


</style>
