<template lang="html">
  <div>
    <NavigationDrawer/>

    <v-content>
      <v-snackbar
        v-model="snackbar"
        top
        right
      >
        
        <v-btn
          color="pink"
          text
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </v-snackbar>

      <Indeterminate v-if="loading"/>
      <div v-else>
        <div v-if="haveNumber" class="mt-12">
          <h1 class="text-center headline">: {{ myNumber }}</h1>

        </div>
        <div v-else class="mt-12">

          <div v-if="attendance">
            <h1 class="text-center headline"></h1>
            <v-row :justify="'center'">
              <v-col cols="11">
                <NowSeatWrapCard/>
              </v-col>
            </v-row>
          </div>

          <div v-else>


            <h1 class="text-center headline"></h1>
            <h1 class="text-center"></h1>

            <v-row class="mt-12" :justify="'center'">

              <v-tooltip v-model="show" top>
                <template v-slot:activator="{ on }">
                  <v-btn outlined x-large fab color="cyan" v-on="on" @click="makeNumber()">
                    <v-icon>mdi-ticket-account</v-icon>
                  </v-btn>
                </template>
                <span></span>
              </v-tooltip>

            </v-row>

          </div>

        </div>
      </div>


    </v-content>
  </div>

</template>

<script>
import firebase from 'firebase/app'
import 'firebase/database'
import 'firebase/storage'
import NavigationDrawer from './NavigationDrawer'
import Indeterminate from './Indeterminate'
import NowSeatWrapCard from './NowSeatWrapCard'

export default {
  data() {
    return {
      loading: true,
      show: false,
      haveNumber: false,
      attendance: false, // 
      myNumber: '',
      realName: '',
      snackbar: false
    }
  },
  components: {
    NavigationDrawer,
    Indeterminate,
    NowSeatWrapCard
  },
  created() {
    this.init()
  },
  methods: {
    async init() {
      let attendanceData = await this.getAttendanceData()
      console.debug(attendanceData)
      if (await this.searchMyNumber(attendanceData)) {
        console.debug("")
        this.haveNumber = true
        let myNumber = await this.getMyNumberData(attendanceData)
        this.myNumber = myNumber
      } else if (await this.searchMyuidNow()) {
        console.debug("")
        this.attendance = true
      } else {
        console.debug("")
      }

      this.loading = false

    },
    getAttendanceData() { // attendance
      let database = firebase.database()
      let attendance = database.ref('attendance')
      let self = this
      return new Promise(function(resolve) {
        attendance.on('value', (snapshot)=> {
          let val = snapshot.val()
          delete val['nil'] // 
          self.attendanceData = val
          resolve(val)
        })
      })
    },
    searchMyNumber(attendanceData) { // attendanceData
      let myUid = firebase.auth().currentUser.uid

      return new Promise(function(resolve) {
        for (let key in attendanceData) {
          if (attendanceData[key]['uid'] == myUid) {
            resolve(true)
          }
        }
        resolve(false)
      })
    },
    searchMyuidNow() { // (now)uid()
      let database = firebase.database()
      let myUid = firebase.auth().currentUser.uid

      // Now
      return new Promise(function(resolve) {
        let now = database.ref('/seats/Now')
        now.on('value', (snapshot)=> {
          let val = snapshot.val()
          let answer = false

          // 
          search_loop:
          for (let i=0; i<15; i++) {
            for (let j=0; j<6; j++) {
              if (val[i][j] == myUid) {
                answer = true
                break search_loop
              }
            }
          }
          resolve(answer)
        })
      })
    },
    async makeNumber() { // 
      // 
      let attendanceData = await this.getAttendanceData()
      let random = 0
      let loop = true
      while (loop) {
        let branch = true
        random = Math.floor( Math.random() * 900000 ) + 100000; // 100000~999999
        for (let key in attendanceData) {
          if (random == key) {
            branch = false
            break
          }
        }
        if (branch) {
          // 
          this.setNumber(random)
          loop = false
        }
      }
    },
    async setNumber(number) { // 
      let self = this
      console.debug("setNumber",number)
      let seat = await this.searchMyReservation()
      if (seat == "") {
        this.snackbar = true
      } else {
        console.debug(seat)

        // 1
        let timeLimit = new Date()
        timeLimit.setMinutes(timeLimit.getMinutes() + 1)
        console.debug('timeLimit', timeLimit)

        let database = firebase.database()

        let result = new Promise(function(resolve) {
          database.ref(`attendance/${number}`).set({'uid': firebase.auth().currentUser.uid,
                                                    'seat': seat,
                                                    'timeLimit': {Hours: timeLimit.getHours(),
                                                                  Minutes: timeLimit.getMinutes()}
                                                  })
          resolve()
        })
        result.then(function() {
          self.$router.go({path: self.$router.currentRoute.path, force: true}) // 
        })
      }



    },
    searchMyReservation() { // 
      let database = firebase.database()
      let myUid = firebase.auth().currentUser.uid
      let now = new Date()
      console.log(now)
      let week = ["Mon","Tue","Wed","Thu","Fri"]


      if (now.getDay() != 0 && now.getDay() != 6) {
        let dayOfWeek = week[now.getDay()-1]

        let reservation = database.ref(`seats/${dayOfWeek}`)

        return new Promise(function(resolve) {
          reservation.on('value', (snapshot)=> {
            let val = snapshot.val()
            let seat = "nil"

            search_loop:
            for (let i=0; i<15; i++) {
              for (let j=0; j<6; j++) {
                if (val[i][j] == myUid) {
                  seat = i
                  break search_loop
                }
              }
            }
            resolve(seat)
          })
        })
      } else {
        return new Promise(function(resolve) {
          console.log('')
          resolve('')
        })
      }

    },
    getMyNumberData(attendanceData) { // 
      let myUid = firebase.auth().currentUser.uid

      return new Promise(function(resolve) {
        for (let key in attendanceData) {
          if (attendanceData[key]['uid'] == myUid) {
            resolve(key)
          }
        }
      })
    }
  }
}
</script>

